html,
body {
  height: 100%;
}
body {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  background-color: #fcffff;
}
body a {
    text-decoration: none;
    color: #d1dbbd;
}
body a:hover {
      color: #91aa9d;
}
#app {
  display: grid;
  height: 100%;
  margin: 0 auto;
  grid-template-areas: 'header   header' 'editor  preview';
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 60px 1fr;
}

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  color: #ffffff;
  background-color: #000000;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-area: header;
}

.editor {
  grid-area: editor;
}
.editor textarea {
    display: block;
    width: 100%;
    height: 100%;
    padding: 50px;
    cursor: text;
    border: none;
}
.editor textarea:focus {
      outline: none;
}

.preview {
  padding-left: 20px;
  border-left: 1px solid rgba(145, 170, 157, 0.7);
  grid-area: preview;
}
